.Card {
	width: 100%;
	background-color: #fff;
	padding: 2rem;

	.ImgContainer {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 4rem;

		img {
			width: 40%;
		}
	}

	h4 {
		text-align: center;
		margin-bottom: 2rem;
		text-decoration: none;
		color: #1e1666;
		font-family: Poppins;
		font-size: 2.2rem;
	}

	p {
		color: #6a7c92;
		font-size: 1.5rem;
	}
}
